@keyframes smoothAppear {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}

.home-wrapper{
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Noto Sans KR', sans-serif;
  .home-title{
    opacity: 0;
    animation: smoothAppear 1s forwards;
    animation-delay: 0.5s;
    margin: 15px 0;
    font-size: 2.4rem;
    font-weight: bold;
    color: midnightblue;
    span{
      font-size: 2.7rem;
      font-family: 'Pacifico', cursive;
    }
  }
  .home-contents{
    opacity: 0;
    animation: smoothAppear 1s forwards;
    animation-delay: 1s;
    margin: 15px 0;
    font-size: 1.5rem;
    color: #282c34;
  }
  .about-project{
    opacity: 0;
    animation: smoothAppear 1s forwards;
    animation-delay: 1.5s;
    margin: 15px 0;
    font-size: 1.5rem;
    color: #282c34;
    text-align: center;
    span{
      font-size: 1.8rem;
      font-weight: bold;
      color: blue;
    }
  }
  .my-website{
    opacity: 0;
    animation: smoothAppear 1s forwards;
    animation-delay: 2.0s;
    margin: 15px 0;
    text-align: center;
    .my-website-title{
      margin-bottom: 5px;
      font-size: 1.8rem;
      font-weight: bold;
      color: midnightblue;
    }
    a{
      font-size: 1.5rem;
      margin: 0 10px;
    }
  }
}

/* 최근게시물 스킨 (latest) */
.lt_basic {width:100%;}
.lt_basic .lt_title {display:block;padding:0 0 20px;border-bottom:1px solid #e9e9e9;text-align:left;font-size:1.2em}

.lt_basic ul {margin:0;padding:0;list-style:none}
.lt_basic li {border-bottom:1px solid #f7f7f7;line-height:24px;text-align:left;padding:15px 15px 10px 0}
.lt_basic li:last-child {border-bottom:0}
.lt_basic .lt_writer img {width:25px;height:25px;border-radius:25px}
.lt_basic .lt_tit {display:inline-block;font-size:1.083em;vertical-align:top;font-weight:bold;overflow:hidden}

.lt_basic li .fa-download {width:16px;height:16px;line-height:16px;background:#ecaa30;color:#fff;text-align:center;font-size:10px;border-radius:2px}
.lt_basic li .fa-link {width:16px;height:16px;line-height:16px;background:#2aa974;color:#fff;text-align:center;font-size:10px;border-radius:2px}
.lt_basic li .fa-heart {color:#ff0000}
.lt_basic li .fa-lock {display:inline-block;width:16px;line-height:14px;font-size:0.833em;color:#fff;background:#262b88;text-align:center;border-radius:2px;border:1px solid #262b88}
.lt_basic li .new_icon {display:inline-block;width:16px;line-height:14px;font-size:0.833em;color:#fff;background:#3fd49e;border:1px solid #32c791;text-align:center;border-radius:2px;margin-left:3px}
.lt_basic li .hot_icon {display:inline-block;width:16px;line-height:14px;font-size:0.833em;color:#fff;background:#ff7261;border:1px solid #e75341;text-align:center;border-radius:2px}
.lt_basic li .fa-caret-right {color:#bbb}
.lt_basic li.empty_li {min-height:350px;text-align:center;line-height:25;color:#777}
.lt_basic .lt_date {float:right;color:#8c8c8c}

.lt_basic .lt_more {position:absolute;top:15px;right:25px;display:block;line-height:15px;padding:5px;color:#bfc2c4;text-align:center;font-size:1.2em;font-weight:bold}

.lt_basic li:hover {transition-duration:.5s}
.lt_basic li:hover {border-left:4px solid #f7f7f7;padding-left:10px}

@media (max-width:767px){
    .lt_basic {padding:20px;}
}

@media (max-width:430px){
    .lt_basic .lt_tit {max-width:65%;text-overflow:ellipsis;white-space:nowrap}
}